.course-add-course {
  display: flex;
  align-items: center;
  justify-content: center;
}

.course-add-course h2 {
  margin: 0;
}

.add-modal {
  max-height: 90%;
  overflow: auto;
}

.add-modal button {
  width: calc(100% - 16px);
  max-width: unset;
}

.add-modal h2 {
  text-align: center;
}