.topic {
  padding-left: 29px;
  padding-right: 29px;
  flex-direction: column;
  justify-content: unset;
  width: calc(100% - 58px);
  overflow-x: hidden;
}

.topic h2 {
  font-weight: 700;
  font-size: 26px;
  margin: 0;
  margin-bottom: 10px;
}

.topic-vertical-block h2 {
  margin: 0;
  font-weight: 600;
  font-size: 22px;
}

.course-vertical-block {
  display: flex;
  height: fit-content;
  cursor: pointer;
  transition: 0.5s filter;
  background-color: white;
  margin: 0;
  margin-bottom: 19px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.course-vertical-block b {
  font-size: 19px;
}

.course-vertical-block span:last-child {
  margin-top: 8px;
}

.course-vertical-block .course-item-list-image {
  min-width: 350px;
  width: 350px;
}

.course-vertical-block:hover {
  filter: brightness(0.9);
}

.course-vertical-block .info {
  display: flex;
  flex-direction: column;
  margin-left: 12px;
  margin-top: 8px;
}

.course-vertical-block .info b {
  font-weight: 600;
  font-size: 26px;
}

.course-vertical-block .info button {
  border-radius: 22px;
  padding: 6px 32px;
  background-color: var(--blue);
  color: white;
  border: 0;
  font-weight: 500;
  font-size: 16px;
  cursor: pointer;
  width: fit-content;
}

.course-vertical-block span {
  color: #55575C;
  font-weight: 500;
  font-size: 17px;
}

.course-vertical-block p {
  font-weight: 500;
  font-size: 15px;
  margin-top: 6px;
  margin-bottom: 13px;
  color: #838690;
}


@media screen and (max-width: 728px) {
  .course-vertical-block {
    flex-direction: column;
    align-items: center;
    padding-left: 0;
    width: calc(100% - 2px);
  }

  .course-vertical-block .info {
    margin-left: 0;
    text-align: center;
    margin-top: 24px;
    margin-bottom: 8px;
    align-items: center;
  }

  .topic {
    padding-left: 8px;
    padding-right: 8px;
    width: 100%;
  }
}