.interest-selection {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.interest-selection-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-height: calc(90% - 32px);
  text-align: center;
  background-color: white;
  width: 80%;
  max-width: 800px;
  padding-left: 8px;
  padding-right: 8px;
}

.interest-selection-topics-list {
  display: flex;
  flex-wrap: wrap;
  overflow-y: auto;
  margin-bottom: 16px;
  align-items: center;
  justify-content: center;
}

.topic-item {
  margin: 8px;
  color: var(--darker-gray);
  background-color: var(--light-gray);
  border-radius: 4px;
  padding: 6px;
  user-select: none;
  cursor: pointer;
  transition: 0.5s filter;
}

.topic-item:hover {
  filter: brightness(90%);
}

.topic-item-selected {
  color: white;
  background-color: var(--darker-blue);
}

.interest-selection-subtitle {
  color: var(--darker-gray);
  margin-bottom: 16px;
}

@media screen and (max-width: 728px) {
  .interest-selection {
    padding-left: 8px;
    padding-right: 8px;
  }

  .interest-selection-block {
    width: calc(100% - 16px);
  }
}