.course-playlist {
  width: 40%;
  min-width: 350px;
  overflow-y: auto;
  height: calc(100% - 51px);
  margin-left: 16px;
}

.course-playlist::-webkit-scrollbar {
  width: 8px;
}

.course-playlist::-webkit-scrollbar-track {
  background: var(--light-gray);
}

.course-playlist::-webkit-scrollbar-thumb {
  background: var(--dark-gray);
}
  
.course-playlist::-webkit-scrollbar-thumb:hover {
  background: #888;
}

.course-topics-list-block {
  position: relative;
  width: calc(100% - 52px);
  padding: 18px 26px;
  margin: 0;
  background-color: white;
}

.course-topics-list-block h2 {
  margin-bottom: 0px;
  font-size: 18px;
  font-weight: 600;
}

.course-topics-list-number-container {
  margin-bottom: 18px;
}

.course-topics-list-number {
  font-size: 16px;
  color: #55575C;
  font-weight: 500;
}
  
.course-video {
  background-color: white;
  color: var(--darker-gray);
  display: flex;
  flex-direction: column;
  font-size: 18px;
  cursor: pointer;
  border: 1px solid rgba(85, 87, 92, 0.15);
  padding: 1.6rem 2rem;
  border-radius: 8px;
  margin-bottom: 8px;
  position: relative;
  transition: background-color 0.3s;
}

.course-video:hover {
  background-color: var(--light-gray);
}
  
.course-video b {
  color: black;
}

.course-video-finished {
  background-color: #2b8e3b0f;
  border: 1px solid var(--dark-blue);
}

.course-video-finished:hover {
  background-color: #2b8e3b2b;
}

.course-video-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.course-video-finished-icon {
  color: var(--dark-blue);
  position: absolute;
  cursor: pointer;
  top: 12px;
  right: 12px;
}