.left-menu {
  min-width: 260px;
  height: 100%;
  background-color: white;
  display: flex;
  flex-direction: column;
  user-select: none;
  padding: 22px;
}


.left-menu h1 {
  margin: 0;
  font-weight: bold;
  font-size: 26px;
  color: var(--blue);
  margin-bottom: 16px;
}

.left-menu-button {
  display: flex;
  align-items: center;
  padding: 11px 14px;
  color: #313131;
  border-radius: 18px;
  font-weight: 500;
  cursor: pointer;
  background-color: white;
  transition: 0.5s filter;
  margin-bottom: 6px;
}

.left-menu-button:hover {
  filter: brightness(0.9);
}

.left-menu-button-selected {
  background-color: var(--blue);
  color: white;
}

.left-menu-button span {
  margin-left: 8px;
}

@media screen and (max-width: 728px) {
  .left-menu {
    display: none;
  }
}