.certificate h2, .certificate p {
  margin-left: 8px;
}

.certificate p {
  color: var(--darker-gray);
}

.certificate button {
  padding-left: 16px;
  padding-right: 16px;
  margin-bottom: 12px;
}

.certificate-block {
  margin-top: 0;
}

.cpf-modal {
  align-items: center;
  justify-content: center;
  text-align: center;
}

.cpf-modal p {
  color: var(--darker-gray);
}

@media screen and (max-width: 728px) {
  .certificate-block {
    width: calc(100% - 40px) !important;
    margin: 0;
    min-height: 60%;
    max-height: fit-content;
    margin-bottom: 12px;
  }
}