.start {
  padding-left: 29px;
  padding-right: 29px;
  flex-direction: row;
  align-items: unset;
}

.start-left {
  width: 100%;
}

.start-left img {
  width: 100%;
}

.topics-list {
  display: flex;
  margin-top: 16px;
  margin-bottom: 8px;
  flex-wrap: wrap;
}

.topics-list a {
  background-color: white;
  padding: 6px 23px; 
  border-radius: 26px;
  font-weight: 600;
  margin-right: 8px;
  user-select: none;
  cursor: pointer;
  transition: 0.5s background-color;
  margin-bottom: 8px;
  text-decoration: none;
  color: black;
}

.topics-list a:hover {
  color: white;
  background-color: var(--blue);
}

.course-list {
  background-color: white;
  border-radius: 16px;
  padding: 16px 20px;
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
}

.course-list h3 {
  margin: 0;
  font-weight: 600;
  font-size: 22px;
  margin-bottom: 12px;
}

.course-list-courses {
  display: flex;
}

.course-list-course {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-right: 11px;
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer;
  transition: 0.5s background-color;
  border-radius: 12px;
  user-select: none;
}

.course-list-course:hover {
  background-color: #f1f1f1;
}

.course-list .course-list-course:last-child {
  margin-right: 0;
}

.course-list-course-image {
  background-image: url(https://i.imgur.com/r1yZHIn.jpg);
  background-position: center;
  background-size: cover;
  width: 100%;
  aspect-ratio: 247/153;
  border-radius: 16px;
  position: relative;
}

.course-list-course-image-university {
  position: absolute;
  background-color: white;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  top: 8px;
  left: 8px;
}

.course-list-course-image-university img {
  width: 20px;
}

.course-list-course b {
  font-weight: 500;
  font-size: 18px;
  margin-top: 4px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.course-list-course p {
  font-weight: 500;
  font-size: 15px;
  color: #838690;
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
}

.course-list-course-timer {
  display: flex;
  color: #838690;
}

.course-list-course-timer div {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 14px;
  color: #838690;
}

.course-list-course-timer span {
  margin-left: 5px;
}

.course-list-course button {
  margin-top: 5px;
  border-radius: 22px;
  padding: 5px 28px;
  background-color: var(--blue);
  color: white;
  border: 0;
  font-weight: 500;
  font-size: 14px;
  cursor: pointer;
  width: fit-content;
}

.course-list-title-info {
  display: flex;
  align-items: center;
}

.course-list-title-info a {
  margin-left: 13px;
  color: #55575C;
  font-weight: 600;
  text-decoration: none;
  height: fit-content;
  margin-bottom: 12px;
}

.course-list-course-progress {
  position: absolute;
  bottom: 9px;
  right: 11px;
  background: #00BD35;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  font-weight: 500;
  font-size: 12px;
  color: #FFFFFF;
  padding: 2px 21px;
}

.course-list-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.course-list-title-arrows {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 12px;
  color: var(--blue);
}

@media screen and (max-width: 728px) {
  .course-list-courses {
    flex-direction: column;
  }

  .course-list-course {
    margin-bottom: 18px;
    margin-right: 0;
  }

  .start {
    padding-left: 8px;
    padding-right: 8px;
  }
}