.search {
  padding-left: 29px;
  padding-right: 29px;
  flex-direction: column;
  justify-content: unset;
  width: calc(100% - 58px);
  overflow-x: hidden;
}

.topic-vertical-block {
  display: flex;
  flex-direction: column;
}

.topic-vertical-block span {
  color: var(--darker-gray);
}

.loading-block {
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading-block h2 {
  margin: 0;
}