.basic-screen {
  display: flex;
  flex-direction: column;
  background-color: #F5F7FB;
  width: calc(100% - 304px);
}

.basic-screen-top {
  display: flex;
  padding: 22px 29px;
  align-items: center;
  justify-content: space-between;
  z-index: 1;
}

.basic-screen-search {
  width: 60%;
  max-width: 410px;
  border-radius: 22px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 11px 15px;
}

.basic-screen-search input[type="text"] {
  border: none;
  background-color: transparent;
  flex: 1;
  margin-left: 8px;
  color: #838690;
  font-size: 16px;
  width: 100%;
  padding: 0;
}

.basic-screen-inner {
  display: flex;
  flex-direction: column;
  width: calc(100%);
  height: calc(100%);
  overflow: auto;
}

.basic-screen-avatar {
  border-radius: 100px;
  width: 40px;
}

.basic-screen-search form {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.basic-screen-search button {
  border: 0;
  height: 30px;
  color: var(--dark-gray);
  margin: 0;
  padding-right: 0;
}

.basic-screen-top-home {
  cursor: pointer;
  display: none;
}

.basic-screen-top h1 {
  color: var(--blue);
  margin: 0;
  margin-left: calc(35px - 8px);
  font-weight: 700;
  font-size: 32px;
  cursor: pointer;
}

.basic-screen-top-right {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: flex-end;
}

.basic-screen-top-right span {
  font-weight: 500;
  color: black;
  user-select: none;
  margin-left: 8px;
}

.basic-screen-top-home {
  display: hidden;
  height: 30px;
  color: var(--blue);
  background-color: white;
  padding: 8px;
  border-radius: 22px;
}

@media screen and (max-width: 728px) {
  .basic-screen {
    max-width: unset;
    width: 100%;
  }

  .basic-screen-top-home {
    display: block;
  }

  .basic-screen-top-right, .basic-screen h1 {
    display: none;
  }

  .basic-screen-search {
    width: 100%;
  }

  .basic-screen-top-home {
    display: block;
    margin-right: 12px;
  }

  .basic-screen-top {
    padding-left: 8px;
    padding-right: 8px;
  }
}